<template>
    <div>
        <top-nav title="订单列表"></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <p>1).拼团订单需要 拼团成功</p>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form :inline="true" :model="search" class="search-form" size="mini">
                    <el-form-item label="订单状态">
                        <el-select v-model="search.order_status" placeholder="订单状态">
                            <el-option label="全部状态" :value="0"></el-option>
                            <el-option v-for="(item,key) in status.status_mean" :label="item.value" :value="item.key+1" :key="key"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="支付状态">
                        <el-select v-model="search.pay_status" placeholder="支付状态">
                            <el-option label="全部状态" :value="0"></el-option>
                            <el-option v-for="(item,key) in status.pay_status_mean" :label="item.value" :value="item.key+1" :key="key"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="发货状态">
                        <el-select v-model="search.shipping_status" placeholder="发货状态">
                            <el-option label="全部状态" :value="0"></el-option>
                            <el-option v-for="(item,key) in status.shipping_status_mean" :label="item.value" :value="item.key+1" :key="key"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="支付方式">
                        <el-select v-model="search.pay_type" placeholder="支付方式">
                            <el-option label="全部状态" :value="0"></el-option>
                            <el-option v-for="(item,key) in status.pay_type_mean" :label="item.value" :value="item.key+1" :key="key"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="订单来源">
                        <el-select v-model="search.prom_type" placeholder="支付方式">
                            <el-option label="全部状态" :value="0"></el-option>
                            <el-option v-for="(item,key) in status.prom_type" :label="item.value" :value="item.key+1" :key="key"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="下单设备">
                        <el-select v-model="search.device" placeholder="下单设备">
                            <el-option label="weixin" value="weixin"></el-option>
                            <el-option label="ali" value="ali"></el-option>
                            <el-option label="baidu" value="baidu"></el-option>
                            <el-option label="toutiao" value="toutiao"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="下单日期">
                        <date-between  v-model="search.date"></date-between>
                    </el-form-item>
                    <el-form-item label="关键字">
                        <el-input placeholder="请输入内容" v-model="search.keywords" class="input-with-select">
                            <el-select style="width: 90px" v-model="search.keywords_type" slot="prepend" placeholder="请选择">
                                <el-option label="订单号" :value="1"></el-option>
                                <el-option label="收货人" :value="2"></el-option>
                            </el-select>
                        </el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button size="mini" type="primary" @click="getOrderList(1)" icon="el-icon-search">搜索</el-button>
                    </el-form-item>
                </el-form>
                <el-table :data="datas.list" style="width: 100%" >
                    <el-table-column width="80" prop="order_id" label="订单ID"></el-table-column>
                    <el-table-column prop="order_sn" label="订单号" ></el-table-column>
                    <el-table-column prop="name" label="收货人"></el-table-column>
                    <el-table-column  label="总金额" width="120">
                        <template slot-scope="scope">
                            <span class="wyu-color-red">￥：{{scope.row.goods_price}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="应付金额" width="120">
                        <template slot-scope="scope">
                            <span class="wyu-color-red">￥：{{scope.row.pay_price}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="order_status" label="订单状态" width="100"></el-table-column>
                    <el-table-column prop="pay_status" label="支付状态" width="100"></el-table-column>
                    <el-table-column prop="shipping_status" label="发货状态" width="100"></el-table-column>
                    <el-table-column prop="pay_type" label="支付方式" width="100"></el-table-column>
                    <el-table-column prop="prom_type_mean" label="订单来源" width="120"></el-table-column>
                    <!--<el-table-column prop="device" label="下单设备" width="120"></el-table-column>-->
                    <el-table-column prop="add_time" label="下单时间"></el-table-column>
                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <div class="list-table-action">
                                <router-link :to="'/order/product/view/'+scope.row.order_id">
                                    <el-button type="text" icon="el-icon-view">查看</el-button>
                                </router-link>
                                <el-button v-if="scope.row.delete == 1" @click="deleteAdmin(scope.row.order_id)" style="margin-left: 14px" type="text" icon="el-icon-delete">删除</el-button>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
                <div style="text-align: right" class="block">
                    <el-pagination @current-change="getOrderList" :page-size="datas.limit"
                                   layout="total, prev, pager, next" :total="datas.count">
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import * as api from '@/api.js';
    import TopNav from "@/components/TopNav";
    import DateBetween from "../../../components/DateBetween";
    export default {
        name: "customerIndex",
        components: {DateBetween, TopNav},
        data: function () {
            return {
                //整体菊花
                select:1,
                loading:false,
                search:{
                    date:[],
                    keywords:'',
                    keywords_type:1,
                    pay_status:0,
                    pay_type:0,
                    shipping_status:0,
                    order_status:0,
                    prom_type:0,
                    device:0,
                },
                status:{},
                datas:{},
                userList:[],
            };
        },
        created() {
            this.getOrderList();
        },
        methods: {
            deleteAdmin(orderId) {
                this.$confirm('确认此订单吗？', '温馨提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.loading = true;
                    api.post("/shop/admin.order.goodsorder/delete", {
                        appToken: this.$store.getters.appToken,
                        order_id: orderId,
                    }).then(() => {
                    this.getOrderList();
                    }).catch(() => {
                    });
                }).catch(() => {
                });
            },
            //获取订单列表
            getOrderList(page=1){
                this.loading = true;
                api.post("/shop/admin.order.goodsorder/index", {
                    appToken: this.$store.getters.appToken,
                    page:page,
                    end_add_time:this.search.date[1] ? this.search.date[1] :  0,
                    bg_add_time:this.search.date[0] ? this.search.date[0] : 0,
                    keywords:this.search.keywords,
                    keywords_type:this.search.keywords_type,
                    pay_status:this.search.pay_status,
                    pay_type:this.search.pay_type,
                    shipping_status:this.search.shipping_status,
                    order_status:this.search.order_status,
                    prom_type:this.search.prom_type,
                    device:this.search.device,
                }).then(res => {
                    this.loading = false;
                    this.datas = res.list;
                    if(page <= 1){
                        this.status = res.status;
                    }
                }).catch(() => {
                    this.loading = false;
                });
            }

        }
    };
</script>
